<template>
  <section class="dexview__entry">
    <section
      v-if="pokemon"
      class="dexview__entry__profile"
    >

      <div
        class="dexview__entry__profile__img"
        :class="'pokemon-'+pokemon.types[0]"
      >
        <div><img :src='"@/assets/icon/3d/"+pokemon.slug+".gif"'></div>
      </div>
      
      <section class="dexview__entry__profile__data">
        <div class="dexview__entry__profile__data__name">
          {{pokemon.name}}
        </div>

        <div class="dexview__entry__profile__data__rainbow">
          <div class="dexview__entry__profile__data__threat">
            <div v-if="pokemon.t_level">
              <img v-for="n in pokemon.t_level" :key="n" :src='"@/assets/icon/star.png"'>
            </div>
            <div v-else>
              <img :src='"@/assets/icon/empty.png"'>
            </div>
            <p>Threat Level</p>
          </div>
        </div>
        
      </section>

    </section>
    
    
    <section class="dexview__entry__menu">  
      <div class="dexview__entry__menu__item" @click="moves('Battle')">battle</div>
      <div class="dexview__entry__menu__item dexview__entry__menu__item--left" @click="moves('Items')">items</div>
      <div class="dexview__entry__menu__item dexview__entry__menu__item--middle" @click="moves('Stats')">stats</div>
      <div class="dexview__entry__menu__item dexview__entry__menu__item--right" @click="moves('Spreads')">spreads</div>
      <div class="dexview__entry__menu__item" @click="moves('Moves')">moves</div>
    </section>
    <div class="dexview__entry__info">
      <keep-alive>
        <component :is="selectedData.component" :data="selectedData.data" class="dexview__entry__info--container"></component>
      </keep-alive>
    </div>

  </section>
</template>

<script>
import Moves from "@/components/_PokemonMoves.vue"
import Items from "@/components/_PokemonItems.vue"
import Spreads from "@/components/_PokemonSpreads.vue"
import Battle from "@/components/_PokemonBattle.vue"
import Stats from "@/components/_PokemonStats.vue"

import axios from "axios"
export default {
  props: ["pokemon"],
  components:{
    Moves: Moves,
    Stats: Stats,
    Spreads: Spreads,
    Items: Items,
    Battle: Battle
  },
  data(){
    return {
      selectedData : {}
    }
  },
  methods:{
    moves(component){
      this.selectedData.data = this.pokemon
      this.selectedData.component = component
    }
  },
  watch:{
    pokemon(n){
      if(this.selectedData.component === undefined){
        this.selectedData.component = 'Stats'
      }
      const tier = this.$route.params.tier
      this.selectedData = { data: n, component: "Stats" }
      axios.get(`https://dogars-api.herokuapp.com/${tier}/moves_and_items/${n.key}`) 
        .then(res=>{
          n.moves = res.data.moves
          n.items = res.data.items
        })
    }
  },
};
</script>